import { Link } from "react-router-dom";

export default function Lists({ listValue, title, background }) {
  // const { ContentSummaryRows } = listValue.Sections
  //   ? listValue.Sections[2]
  //   : {
  //       ContentSummaryRows: [
  //         { ContentId: 1, Title: "درحال بارگذاری", EnglishTitle: "" },
  //       ],
  //     };
  const urlimage = "https://cdn.tek-nic.com/media/images/panel/";

  return (
    <div
      className="p-4"
      style={
        (background + "").length > 0
          ? { background: `url("${background}") no-repeat` }
          : null
      }
    >
      <Link className="sm:text-4xl text-2xl">
        {title ?? ""}
        <img src="/icons/arrow.svg" alt="arrow" className="w-9 inline" />
      </Link>
      <div className="relative mt-3">
        <ul className="lists flex flex-nowrap overflow-x-scroll">
          <swiper-container
            update-on-window-resize="true"
            width="130"
            style={{
              "--swiper-navigation-size": "20px",
              "--swiper-navigation-color": "#fff",
            }}
            navigation={true}
          >
            {listValue.map(({ ContentId, Title }) => (
              <swiper-slide>
                <li
                  className="p-1 w-32 flex-shrink-0 mb-2 group"
                  key={ContentId}
                >
                  <Link to={`/contents/${ContentId}`}>
                    <div className="relative">
                      <img
                        loading="lazy"
                        className="rounded-md"
                        src={urlimage + ContentId + "/portrait.jpg"}
                        alt="poster"
                      />
                      <div className="opacity-0 h-full transition group-hover:opacity-100 absolute p-2 leading-loose text-sm bg-[#000000ab] top-0">
                        <p>درام</p>
                        <p>2022/ آمریکا</p>
                        <p>1ساعت و 54 دقیقه</p>
                        <span>
                          <img
                            src="/icons/imdb.svg"
                            alt="imdb"
                            className="inline w-1/4"
                          />
                          <span className="inline mr-4">7.4</span>
                        </span>
                      </div>
                    </div>
                    <p className="mt-1 text-xs">{Title}</p>
                  </Link>
                </li>
              </swiper-slide>
            ))}
          </swiper-container>
        </ul>
      </div>
    </div>
  );
}
