export default function Comment({ commentValue }) {
  return (
    <ul>
      {commentValue.map(({ Body }) => (
        <li className=" m-3 bg-zinc-900 rounded-md">
          <div className="flex items-center gap-2">
            <img
              src="https://www.gapfilm.ir/icon.png"
              alt="profile"
              className="w-14 bg-gray-900  rounded-lg"
            />
            <span>کاربر ناشناس</span>
          </div>
          <p className="p-3">{Body}</p>
        </li>
      ))}
    </ul>
  );
}
