export default function Home() {
  //------- Get and Set data form api
  const [homedata, sethomedata] = useState([]);
  // movies(ParentEntityId=6) kids(AgeRangeId=2) serise(ParentEntityId=6)
  //
  async function uri() {
    const index = "https://core.gapfilm.ir/api/v3.3/";
    const homepage =
      index +
      "GetFirstPageByPlatform?PageSize=8&PageIndex=0&ContentRows=11&PlatformId=1";

    const response = await fetch(homepage);
    const {
      Result: { Sections, PlatformSummaryInfos },
      Status,
    } = await response.json();
    sethomedata(Sections);
  }
  /*   async function handleLogin(e) {
    e.preventDefault();

    try {
      const user = await Parse.User.logIn("test", "test");
      console.log("User logged in successfully", user);
    } catch (error) {
      console.error("Error logging in", error);
    }
  }
  async function checkLoggedInUser() {
    const user = await Parse.User.currentAsync();
    if (user) {
      console.log("User is logged in", user);
    } else {
      console.log("User is not logged in");
    }
  } */

  useEffect(() => {
    uri();
    //  saveNewPerson();
  }, []);
  let data = [
    { title: "انیمیشن", images: "https://tv.filmnet.ir/images/79/2e/792e3ab8a8554650869a4951f9316921.jpg?w=500" },
    { title: "سریال", images: "https://tv.filmnet.ir/images/11/27/1127ce1f4e3e4a24af46c659ef8a4dc4.jpg?w=500" },
    { title: "اکشن", images: "https://tv.filmnet.ir/images/43/fa/43fa79fb37fe48f3af910845d99a0c3b.jpg?w=500" },
    { title: "ماجراجویی", images: "https://tv.filmnet.ir/images/3d/15/3d152cdb004d46aea72a44eca9e630e5.jpg?w=500" },
    { title: "تاریخی", images: "https://tv.filmnet.ir/images/33/36/3336d2d374984d5aa95d4811ff29e0b8.jpg?w=500" },
    { title: "ترسناک", images: "https://tv.filmnet.ir/images/5a/c7/5ac79b71c73e47f2b044b1623da0bd0d.jpg?w=500" },
    { title: "جنایی", images: "https://tv.filmnet.ir/images/22/d6/22d6bdb83e864fddbd28f68f04ecb324.jpg?w=500" },
    { title: "معمایی", images: "https://tv.filmnet.ir/images/30/76/3076e55fdf1e4db7b52c399cbd50bb79.jpg?w=500" },

  ]
  if (Object.keys(homedata).length !== 0) {
    return (
      <>
        <header>
          <Header />
          <Slider sliderValue={homedata[0]} />
          <div className="relative top-[-54px] z-[1]">
            <h3 className="text-3xl mb-2 px-5">دسته بندی</h3>
            <div className="lists overflow-x-scroll">
              <div className="flex justify-center sm:w-11/12 w-[720px] mx-auto flex-wrap gap-2">
                {
                  data.map(({ images }) => {
                    return (<a href="/" className="w-52">
                      <img
                        src={images}
                        alt="poster"
                        className="rounded "
                      />
                    </a>
                    )
                  })
                }
              </div>
            </div>
          </div>
        </header>
        {homedata.map(({ Description, ContentSummaryRows, Image }) => {
          if (!["اسلایدر"].includes(Description)) {
            let filtertitle = Description.includes("تک بنر") ? "" : Description;
            filtertitle = filtertitle.includes("گپ فیلم") ? "پیشنهاد ما" : filtertitle;
            return (
              <Lists
                title={filtertitle}
                listValue={ContentSummaryRows}
                background={Image}
              />
            );
          }
        })}
        <Footer />
      </>
    );
  }
}
