import { useParams, useLocation } from "react-router-dom";
import { useState, useEffect } from "react";
import HeadHome from "../../Components/Header";
import Footer from "../../Components/Footer";
import Header from "./Header";
import Main from "./Main";
import Lists from "../../Components/Lists";

export default function Index() {
  const { pathname } = useLocation();
  const { id } = useParams();
  //------- Get and Set data form api
  const [homedata, sethomedata] = useState({});

  async function uri() {
    let index = "https://core.gapfilm.ir/api/v3.1/";

    let homepage;
    if (pathname === "/contents/Movies") {
      index = "https://core.gapfilm.ir/api/v3.3/";
      homepage =
        index +
        `GetFirstPageByPlatform?PageSize=8&PageIndex=0&ContentRows=11&PlatformId=1&ParentEntityId=6&ParentType=2`;
    } else if (pathname === "/contents/Serial") {
      index = "https://core.gapfilm.ir/api/v3.3/";
      homepage =
        index +
        `GetFirstPageByPlatform?PageSize=8&PageIndex=0&ContentRows=11&PlatformId=1&ParentEntityId=7&ParentType=2`;
    } else if (pathname === "/contents/Kids") {
      index = "https://core.gapfilm.ir/api/v3.3/";
      // homepage =
      //   index +
      //   `GetFirstPageByPlatform?PageSize=8&PageIndex=0&ContentRows=11&PlatformId=1&ParentEntityId=7&ParentType=2`;
    } else {
      homepage = index + `GetContent?Id=${id}&PageIndex=0`;
    }

    const response = await fetch(homepage);

    if (!["/contents/Serial", "/contents/Kids", "/contents/Movies"].includes(pathname)) {
      const { Result, Status } = await response.json();
      sethomedata(Result);
    } else {
      const { Result: { Sections } } = await response.json();
      sethomedata(Sections);
    }
  }

  useEffect(() => {

    uri();
    //  saveNewPerson();
  }, [pathname]);

  console.log(homedata);
  console.log(pathname);

  if (Array.isArray(homedata) || ('Body' in homedata)) {
    return (
      <>
        <HeadHome />
        {!["/contents/Serial", "/contents/Kids", "/contents/Movies"].includes(pathname) && !Array.isArray(homedata) ? (
          <Header headerValue={homedata} />

        ) : null}

        {["/contents/Serial", "/contents/Kids", "/contents/Movies"].includes(pathname) && Array.isArray(homedata) ? (
          <div className="pt-20">
          {homedata.map(({ Title, ContentSummaryRows, Image }) => {
            if (!Title.includes("اسلایدر")) {
              return (
                <Lists
                  title={Title}
                  listValue={ContentSummaryRows}
                  background={Image}
                />
              );
            }
          })}
          </div>
        ) : (
          <Main mainValue={homedata} />
        )}

        <Footer />
      </>
    );
  }
}
